import {enginePagesConfig} from "./Config";


const  container = document.querySelector("section#portfolio.container") as HTMLDivElement
const projectItemTemplate = document.querySelector("template#project-item") as HTMLTemplateElement

console.log(projectItemTemplate.content)


function createProjectItem(vars: EnginePageConfigVars, itemInfo: EnginePageProjectConfig)
{
    const newItem = projectItemTemplate.content.cloneNode(true) as HTMLDivElement
    const title = newItem.querySelector("h3") as HTMLHeadingElement
    const image = newItem.querySelector("img") as HTMLImageElement
    image.src = itemInfo.image
    const link = newItem.querySelector("a") as HTMLAnchorElement
    title.textContent = itemInfo.name
    switch (itemInfo.link.type)
    {
        case "GitHub":
            link.href = `${vars.gitHubPageURL}/${itemInfo.link.repoName}`
            link.textContent = "GitHub"
            break
        case "SubPage":
            let pageName = itemInfo.link.pageName;
            link.href = `${pageName.endsWith(".html") ? pageName.replace(".html", "") : pageName}.html`
            link.textContent = "Page"
            break
        default:
            //TODO: handle other link types
    }
    return newItem
}

function setupEnginePage(config: EnginePageConfig)
{
    console.log(config)
    const {variables, pages} = config
    const vars = variables ?? {gitHubPageURL: ""}   satisfies EnginePageConfigVars
    const row = document.createElement("div") as HTMLDivElement
    row.classList.add("row")
    console.log(pages!)
    for (const page of pages!)
    {
        row.append(createProjectItem(vars, page))
    }
    console.log(row)
    container.append(row)
}


window.onload = _ => setupEnginePage(enginePagesConfig.Unity!)


